
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'

import {mapState} from 'vuex'
import WaKuang from './wakuang/WaKuang'
import My from './myinfo/My'

import Activity from './wakuang/Activity'

export default {
components: {
ViewBox,
Group,
Cell,
XButton,
XHeader,
Tabbar,
TabbarItem,
'wakuang': WaKuang,
'my': My,
'activity': Activity
},
data () {
return {




msg: 'Hello World!',
tabIndex: 2


}
},
computed: {
...mapState({
currentView: state => state.vux.homeView,
showHeader: state => state.vux.showHeader
})
},
methods: {
changeView (view) {
if (view === 'wakuang') {
this.showHeader = false
this.$store.commit('updateHeaderStatus', false)
} else {
this.$store.commit('updateHeaderStatus', true)
}
this.currentView = view
this.$store.commit('updateHomeView', view)
},
back () {

this.$router.back(-1)
},
gotoHome () {
this.$router.push({name: 'home'})

}
}
}



